;(function(w){
    function IeUpdater(version) {
        //version 代表最低兼容版本 6,7,8,9
        this.version = version||8;
        // 是否可关闭
        this.closable = false;
        this.render(this.version);
    }

IeUpdater.prototype = {
    template:function() {
        var d = document.createElement('div');
        var tpl = '';
        // var base_url = 'http://static.test.tf56.com/ehuodi/libs/ie-updater/';
        // var base_url = 'http://127.0.0.1:8006/ie-updater/';
        var base_url = 'https://static.tf56.com/ehuodi/libs/ie-updater/';

        var browser = {
            safe360:'http://se.360.cn/',
            firefox:'http://download.firefox.com.cn/releases-sha2/stub/official/zh-CN/Firefox-latest.exe',
            chrome:'http://rj.baidu.com/soft/detail/14744.html'
        };

        //页面结构
        tpl +=
            '<div class="updater-layer"></div>'+
            '<div class="updater-box">'+
                '<a href="'+browser.firefox+'" class="updater-firefox" target="_blank">火狐浏览器</a>'+
                '<a href="'+browser.chrome+'" class="updater-google" target="_blank">谷歌浏览器</a>';
        if(this.closable){
            tpl+='<span class="updater-close" id="updaterClose"></span></div>';
        }else{
            tpl+='</div>';
        }

        // 基本样式
        tpl+=
            '<style>.updater-layer{position:fixed;_position:absolute;left:0;top:0;bottom:0;right:0;width:100%;height:100%;background-color:#000;filter:alpha(opacity=60);opacity:.6;z-index:99999999}'
            +'.updater-box{position:fixed;_position:absolute;left:50%;top:50%;margin-left:-295px;margin-top:-182px;width:590px;height:364px;background:url('+base_url+'img/ie-updater-bg.png) no-repeat;z-index:100000000}'
            +'.updater-box a{position: absolute;text-decoration: none;color:#666;font-size:14px;width:70px;height:10px;padding-top:60px;background-color:#FFF;text-align:center;line-height:32px;cursor: pointer;}'
            +'.updater-360{background:url('+base_url+'img/360.jpg) no-repeat center top;top:215px;left:100px;}'
            +'.updater-google{background:url('+base_url+'img/chrome.jpg) no-repeat center top;left:390px;top:215px}'
            +'.updater-firefox{background:url('+base_url+'img/firefox.jpg) no-repeat center top;top:215px;left:150px}'
            +'.updater-close{position:absolute;right:5px;top:5px;display:block;background:url('+base_url+'img/close.png) no-repeat center;width:30px;height:30px;}'
            +'html,body{overflow:hidden;height:100%}</style>';

        d.innerHTML = tpl;
        return d;
    },

    support:function(version) {
        var detection = {
            6: document.all && !window.XMLHttpRequest,
            7: document.all && !document.querySelector,
            8: document.all && !document.addEventListener,
            9: document.all && !window.atob,
            10: !window.WebGLRenderingContext
        };
        return detection[version];
    },

    render:function(version) {
        var condition = this.support(version);
        var d = this.template();
        var closable = this.closable;
        var f = function(){
            var s = document.getElementsByTagName("body")[0];
            if ("undefined" == typeof(s)) {
                setTimeout(f, 50);
            } else {
                s.insertBefore(d, s.firstChild);
            };
        };
        condition&&f();
    }
};

 new IeUpdater(8);
})(window);
